/* Dots */

.slick-dotted.slick-slider {
  margin-bottom: 0;
}

.slick-dots {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  gap: 18px;
  margin-top: 16px;

  @include tablet {
    margin-top: 32px;
  }

  li {
    height: 12px;
    button {
      width: 12px;
      height: 12px;
      border-radius: 50%;
      background-color: #d9d9d9;
      text-indent: -9999px;
      overflow: hidden;
      border: none;
    }
    &.slick-active button {
      // background-image: url('../images/reviews/icon-house.svg');
      background-color: transparent;
      background-size: cover;
      background-position: center;
      border-radius: 1;
    }
  }
}
